import React from 'react';
import './ForgotEmail.scss';
import ReCAPTCHA from 'react-google-recaptcha';
import config from 'Config';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';
import notRobotImg from '../assets/not-robot-img.png';

class ForgotEmailContainer extends React.Component{
  constructor(props){
    super(props);


  }

  goToIndex = () => {
      this.props.router.push('/');
  }

  goToLogin = () => {
    this.props.router.push('/login');
  }

  goToForgotPassword = () => {
    this.props.router.push('/forgot-password');
  }

  onRecaptcha = () => {

  }

  render() {
      return (
          <div className="forgot-form">
              <div className="header-section">
                  <div className="header-items">
                      <div className="header-left">
                          <img src={logoImg} className="droplet-logo" onClick={this.goToIndex}/>
                      </div>
                      <div className="header-right">
                          <label className="header-text pointer" onClick={this.goToLogin}>Login</label>
                      </div>
                  </div>
              </div>

              <div className="container history-container">
                  <div className="row">
                      <div className="offset-1 col-10">
                          <div className="step-section">
                              <h4 className="step-text">Forgot Password?</h4>
                          </div>
                          <div className="password-section">
                              <div className="form-group">
                                  <label htmlFor="pass">Please enter your registered email address</label>
                                  <input type="email" className="form-control" id="email"
                                         placeholder="johndoe@email.com"/>
                              </div>
                              <div>
                                  <ReCAPTCHA ref="recaptcha" sitekey={config.ReCAPTCHA_KEY} onChange={this.onRecaptcha} />
                              </div>
                              <button className="btn btn-green btn-checkout" onClick={this.goToForgotPassword}>RESET AND LOGIN</button>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      );
  }

}

export default ForgotEmailContainer;



